import InputLabel from '@material-ui/core/InputLabel';
import MenuItem from '@material-ui/core/MenuItem';
import Input from '@material-ui/core/Input';
import DeleteIcon from '@material-ui/icons/Delete';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';
import Radio from '@material-ui/core/Radio';
import RadioGroup from '@material-ui/core/RadioGroup';
import Button from '@material-ui/core/Button';
import Tooltip from '@material-ui/core/Tooltip';
import Link from '@material-ui/core/Link';
import { useLocalStore } from "../services/LocalStoreService";
import { socket } from '../context/SocketContext';

const iOSBoxShadow =
    '0 3px 1px rgba(0,0,0,0.1),0 4px 8px rgba(0,0,0,0.13),0 0 0 1px rgba(0,0,0,0.02)';

const MySlider = withStyles({
    root: {
        height: 2,
        padding: '15px 0',
    },
    thumb: {
        height: 28,
        width: 28,
        boxShadow: iOSBoxShadow,
        marginTop: -14,
        marginLeft: -14,
        '&:focus, &:hover, &$active': {
            boxShadow: '0 3px 1px rgba(0,0,0,0.1),0 4px 8px rgba(0,0,0,0.3),0 0 0 1px rgba(0,0,0,0.02)',
            // Reset on touch devices, it doesn't add specificity
            '@media (hover: none)': {
                boxShadow: iOSBoxShadow,
            },
        },
    },
    active: {},
    valueLabel: {
        left: 'calc(-50% + 12px)',
        top: 9,
        '& *': {
            background: 'transparent',
            color: '#FFFFFF',
        },
    },
    track: {
        height: 3,
    },
    rail: {
        height: 2,
        opacity: 0.5,
        backgroundColor: 'secondary',
    },
    mark: {
        backgroundColor: 'secondary',
        height: 8,
        width: 1,
        marginTop: -3,
    },
    markActive: {
        opacity: 1,
        backgroundColor: 'currentColor',
    },
})(Slider);

const headCells = [
    { id: 'coin', align: 'left', disablePadding: true, label: 'Coin' },
    { id: 'change', align: 'center', disablePadding: false, label: 'Distribution Weight' },
    { id: 'targetdistribution', align: 'left', disablePadding: true, label: 'Target Distribution' },
    { id: 'currentdistribution', align: 'left', disablePadding: true, label: 'Current Distribution' },
    { id: 'offset', align: 'right', disablePadding: true, label: 'Coin Offset' },
    { id: 'notifications', align: 'left', disablePadding: false, label: '' },
    { id: 'delete', align: 'right', disablePadding: false, label: '' },
];

const marks = [
    {
        value: -1,
        label: '',
    },
    {
        value: 100,
        label: '',
    },
];

const useTableHeadStyles = makeStyles((theme) => ({
    headcolor:
        theme.palette.type === 'light'
            ? {
                backgroundColor: '#aaaaaa',
            }
            : {
                backgroundColor: '#777777',
            },
    headheight: {
        height: 44,
    },
}));

function EnhancedTableHead(props) {
    const { onSelectAllClick, numSelected, rowCount } = props;
    const classes = useTableHeadStyles();

    let checkbox;
    checkbox = <Checkbox
        indeterminate={numSelected > 0 && numSelected < rowCount}
        checked={rowCount > 0 && numSelected === rowCount}
        onChange={onSelectAllClick}
        inputProps={{ 'aria-label': 'select coins' }}
    />
    return (
        <TableHead>
            <TableRow className={`${classes.headcolor} ${classes.headheight}`}>
                <TableCell padding="checkbox">
                    {checkbox}
                </TableCell>
                {headCells.map((headCell) => (
                    <TableCell
                        key={headCell.id}
                        align={headCell.align}
                        padding={headCell.disablePadding ? 'none' : 'normal'}
                    >
                        <TableSortLabel>
                            {headCell.label}
                        </TableSortLabel>
                    </TableCell>
                ))}
            </TableRow>
        </TableHead>
    );
}

EnhancedTableHead.propTypes = {
    classes: PropTypes.object.isRequired,
    numSelected: PropTypes.number.isRequired,
    onSelectAllClick: PropTypes.func.isRequired,
    rowCount: PropTypes.number.isRequired,
};

const useToolbarStyles = makeStyles((theme) => ({
    root: {
        paddingLeft: theme.spacing(2),
        paddingRight: theme.spacing(1),
        maxWidth: 960,
        minWidth: 960,
    },
    highlight:
        theme.palette.type === 'light'
            ? {
                color: theme.palette.secondary.main,
                backgroundColor: lighten(theme.palette.secondary.light, 0.85),
            }
            : {
                color: theme.palette.text.primary,
                backgroundColor: theme.palette.secondary.dark,
            },
    title: {
        flex: '1 1 100%',
    },
}));

const EnhancedTableToolbar = (props) => {
    const classes = useToolbarStyles();
    const { selected } = props;
    const numSelected = selected.length;

    return (
        <Toolbar
            className={clsx(classes.root, {
                [classes.highlight]: numSelected > 0,
            })}
        >
            {numSelected > 0 ? (
                <Typography className={classes.title} color="inherit" variant="subtitle1" component="div">
                    {(numSelected === 1) ? 'Coin' : 'Coins'} <i>excluded</i> from balancing: {selected.join(', ')}
                </Typography>
            ) : (
                <Typography className={classes.title} variant="subtitle1" id="tableTitle" component="div">
                    Set target distribution and (optionally) select coins to be <i>excluded</i> from balancing
                </Typography>
            )}
        </Toolbar>
    );
};

EnhancedTableToolbar.propTypes = {
    selected: PropTypes.array.isRequired,
    portfolio: PropTypes.array.isRequired,
    setSelected: PropTypes.func.isRequired,
};

const useStyles = makeStyles((theme) => ({
    root: {
        width: '100%',
    },
    paper: {
        width: '800px',
        marginBottom: theme.spacing(2),
    },
    table: {
        maxWidth: 960,
        minWidth: 960,
        "& .MuiTableCell-sizeSmall": {
            paddingTop: "6px",
            paddingBottom: "0px",
        },
    },
    cell: {
        width: 50,
    },
    cell2: {
        width: 120,
    },
    textinput: {
        width: '380px',
    },
    textinput2: {
        width: '120px',
    },
    visuallyHidden: {
        border: 0,
        clip: 'rect(0 0 0 0)',
        height: 1,
        margin: -1,
        overflow: 'hidden',
        padding: 0,
        position: 'absolute',
        top: 20,
        width: 1,
    },
    labelStyle: {
        fontSize: '12px',
        '&.Mui-focused': {
            color: theme.palette.type === 'light'
                ? 'rgba(0, 0, 0, 0.54)'
                : 'rgba(255, 255, 255, 0.7)'
        },
    },
}));

const LightTooltip = withStyles((theme) => ({
    tooltip: {
        backgroundColor: theme.palette.common.white,
        color: 'rgba(0, 0, 0, 0.87)',
        boxShadow: theme.shadows[1],
        fontSize: 12,
    },
}))(Tooltip);

export default function ConfigP2(props) {
    const classes = useStyles();

    const sort = (pf) => {
        return (pf.length > 0) ? pf.sort((a, b) => (a.coin > b.coin) ? 1 : ((b.coin > a.coin) ? -1 : 0)) : [];
    }

    const [basecoins, setBasecoins] = useContext(CoinContext);
    const configuration = useContext(ConfigurationContext)[0];
    const input = useContext(ConfigurationInputContext)[0];
    const newPortfolio = (input.portfolio) ? sort(input.portfolio) : [];
    const [portfolio, setPortfolio] = useState(newPortfolio);
    const [portfolioReceived, setPortfolioReceived] = useState(false);
    const [coinsReceived, setCoinsReceived] = useState(false);
    const [selected, setSelected] = useState([]);
    const [basecoin, setBasecoin] = useState(input.basecoin);
    const { access } = useContext(ServerContext)[0];
    const initialOffset = useRef([]);
    const [openDelete, setOpenDelete] = useState(false);
    const [deleteCoin, setDeleteCoin] = useState('');
    const [marketcapEnabled, setMarketcapEnabled] = useState(input.marketcapEnabled || false);
    const excludedChangeRef = useRef(input.excludedChange || []);
    const offsetChangeRef = useRef(input.offsetChange || []);
    const marketcapEnabledRef = useRef(input.marketcapEnabled || false);
    const mode = useLocalStore('mode', 'light')[0];

    const precision = 2;

    const onInput = (data) => {
        props.callback({
            field: 'portfolio',
            input: data,
        });
    }

    const onBasecoin = (e) => {
        // Reset sliders and excludes if basecoin is changed
        if (input.basecoin !== '') resetPortfolio();

        props.callback({
            field: e.target.name,
            input: e.target.value
        });

        setBasecoin(e.target.value);

        setPortfolioReceived(false);
        getPortfolio();
    }

    const onMarketcap = (e) => {
        const value = e.target.value === 'false' ? false : e.target.value;
        props.callback({
            field: 'marketcapEnabled',
            input: value,
        });
        setMarketcapEnabled(value);
        marketcapEnabledRef.current = value;
        handleSlider();
    }

    const getPortfolio = () => {
        if (socket && access.allow) socket.emit("get-portfolio", {
            exchange: input.exchange,
            apiKey: input.apiKey,
            apiSecret: input.apiSecret,
            apiPassword: input.apiPassword,
            basecoin: input.basecoin,
        }, access.token);
    }

    useEffect(() => {
        let isMounted = true;

        let newSelected = [];
        newPortfolio.forEach((coin) => {
            if (coin.excluded) newSelected.push(coin.coin);
        });
        setSelected(newSelected);

        if (input.basecoin !== '') setBasecoins([input.basecoin]);

        socket.on("portfolio2", data => {
            if (!isMounted) return;

            data = sort(data);
            // Upon initial configuration, basecoin needs to be set
            if (input.basecoin === '' && input.configstate === 'initial') {
                let base = [];
                let usercoins = [];
                let options = ['BTC', 'EUR', 'ETH', 'PAXG', 'USD', 'BUSD', 'TUSD', 'USDT', 'USDC', 'USDP'];

                data.forEach((coin) => {
                    usercoins.push(coin.coin);
                });

                base = options.filter((coin) => {
                    return (usercoins.includes(coin));
                });

                setBasecoins(base);
                setCoinsReceived(true);
            } else {
                // data is false if the API key or secret are invalid, or if the server's IP address is not whitelisted on the exchange
                if (data !== false) {
                    const numCoins = data.length;
                    data.forEach((coin) => {
                        // Check if coin was already in portfolio or is newly added to account
                        const match = input.portfolio.find(c => c.coin === coin.coin);
                        if (!match) {
                            coin.offset = 0;
                            // On initial configuration, don't exclude coins
                            if (input.portfolio.length !== 0) {
                                coin.slider = -1;
                                coin.distribution = (0).toFixed(precision);
                                coin.excluded = true;
                                setSelected(prevState => [...prevState, coin.coin]);
                            } else {
                                coin.slider = -1;
                                coin.distribution = (100 / numCoins).toFixed(precision);
                                coin.excluded = false;
                            }
                        } else {
                            // Keep previously set attributes
                            coin.offset = match.offset || 0;
                            coin.slider = match.slider;
                            coin.distribution = parseFloat(match.distribution).toFixed(precision);
                            coin.excluded = match.excluded;
                        }
                        // Store initial offset
                        initialOffset.current.push({
                            coin: coin.coin,
                            offset: coin.offset
                        });
                    });

                    data = updateCurrentDistribution(data);
                    setPortfolioReceived(true);
                    setPortfolio(data);
                    onInput(data);
                }
            }
        });

        return () => {
            isMounted = false;
            socket.off("portfolio2");
        }
    }, []);

    useEffect(() => {
        // Make sure the target distribution is set properly
        handleSlider();

        return () => {
        }
    }, [selected]);

    const updateCurrentDistribution = (data) => {
        let totalValue = 0;
        data.forEach((c) => {
            if (!c.excluded) {
                const match = initialOffset.current.find((c2) => c2.coin === c.coin);
                const offset = (c.offset === '-' || c.offset === '.') ? Number(0) : Number(c.offset);
                totalValue += Math.max(c.number + offset - Number(match.offset), 0) * c.price;
            }
        });

        data.forEach((c) => {
            if (!c.excluded) {
                const match = initialOffset.current.find((c2) => c2.coin === c.coin);
                const offset = (c.offset === '-' || c.offset === '.') ? Number(0) : Number(c.offset);
                const distr = (Math.max(c.number + offset - Number(match.offset), 0) * c.price / totalValue * 100).toFixed(precision);
                c.currentDistribution = distr;
            } else {
                c.currentDistribution = (0).toFixed(precision);
            }
        });
        return data;
    }

    const resetPortfolio = () => {
        setSelected([]);
        setPortfolio([]);
        onInput([]);
    }

    const handleSelectAllClick = (event) => {
        let newSelected = [];
        let allSelected = false;
        if (selected.length === 0) {
            newSelected = portfolio.map((n) => n.coin).filter(coin => coin !== input.basecoin);
            allSelected = true;
        }
        setSelected(newSelected);

        portfolio.forEach((coin) => {
            handleSlider(coin.slider, coin.coin, newSelected);
            if (coin.coin !== input.basecoin) toggleExcluded(coin.coin, allSelected);
        });

        updateCurrentDistribution(portfolio);
    };

    const handleClick = (coin) => {
        if (coin !== input.basecoin) {
            const selectedIndex = selected.indexOf(coin);

            let newPortfolio = [...portfolio];
            let match = newPortfolio.find(c => c.coin === coin);
            match.excluded = (selectedIndex === -1);
            setPortfolio(newPortfolio);

            let newSelected = [];

            if (selectedIndex === -1) {
                newSelected = newSelected.concat(selected, coin);
            } else if (selectedIndex === 0) {
                newSelected = newSelected.concat(selected.slice(1));
            } else if (selectedIndex === selected.length - 1) {
                newSelected = newSelected.concat(selected.slice(0, -1));
            } else if (selectedIndex > 0) {
                newSelected = newSelected.concat(
                    selected.slice(0, selectedIndex),
                    selected.slice(selectedIndex + 1),
                );
            }

            setSelected(newSelected);
            toggleExcluded(coin, match.excluded);
            handleSlider(match.slider, match.coin, newSelected);
            updateCurrentDistribution(portfolio);
        }
    };

    const toggleExcluded = (coin, excluded) => {
        let original = configuration.portfolio.find(c => c.coin === coin);

        // In case of first-time setup, or if coin is added to the portfolio
        if (!original) original = input.portfolio.find(c => c.coin === coin);

        if (original.excluded !== excluded) {
            let match = excludedChangeRef.current.find(c => c.coin === coin);

            if (match) {
                match.excluded = !match.excluded;
                // const index = excludedChangeRef.current.findIndex(c => c.coin === coin);
                // excludedChangeRef.current.splice(index, 1);
            } else {
                excludedChangeRef.current.push({
                    coin: coin,
                    excluded: excluded,
                });
            }
        } else {
            let match = excludedChangeRef.current.find(c => c.coin === coin);

            if (match) {
                match.excluded = !match.excluded;
                // const index = excludedChangeRef.current.findIndex(c => c.coin === coin);
                // excludedChangeRef.current.splice(index, 1);
            } else {
                // Check if coin was added to the portfolio
                const match2 = input.portfolio.find(c => c.coin === coin.coin);

                if (input.portfolio.length !== 0 && !match2) {
                    excludedChangeRef.current.push({
                        coin: coin,
                        excluded: excluded,
                    });
                }
            }
        }

        props.callback({
            field: 'excludedChange',
            input: excludedChangeRef.current,
        });
    }

    const handleSlider = (value = undefined, coin = undefined, select = selected) => {
        let newPortfolio = [...portfolio];
        if (Number.isInteger(value) && coin) {
            let match = newPortfolio.find(c => c.coin === coin);
            match.slider = value;
        }
        let totalSlider = 0;
        let numSlider = 0;
        let totalMarketcap = 0;
        let totalMarketcapSqrt = 0;

        newPortfolio.forEach((coin) => {
            // Select contains excluded coins
            if (select.indexOf(coin.coin) === -1) {
                totalSlider += coin.slider;
                if (coin.slider === -1) {
                    totalSlider++;
                    totalMarketcap += coin.marketcap;
                    totalMarketcapSqrt += Math.sqrt(coin.marketcap);
                }
                if (coin.slider !== -1) numSlider++;
            }
        });
        let maxSlider;
        const remainingSlider = 100 - totalSlider;
        const numCoinsZero = newPortfolio.length - numSlider - select.length;
        maxSlider = (numCoinsZero === 0) ? totalSlider : Math.max(100, totalSlider);

        newPortfolio.forEach((coin) => {
            coin.excluded = false;
            if (select.indexOf(coin.coin) !== -1) {
                coin.distribution = (0).toFixed(precision);
                coin.excluded = true;
            } else if (maxSlider <= 100) {
                if (coin.slider === -1) {
                    if (totalMarketcap !== 0 && marketcapEnabledRef.current) {
                        if (marketcapEnabledRef.current === 'marketcap') {
                            coin.distribution = (remainingSlider / (totalMarketcap / coin.marketcap)).toFixed(precision);
                        } else if (marketcapEnabledRef.current === 'marketcapsqrt') {
                            coin.distribution = (remainingSlider / (totalMarketcapSqrt / Math.sqrt(coin.marketcap))).toFixed(precision);
                        }
                    } else {
                        coin.distribution = (remainingSlider / numCoinsZero).toFixed(precision);
                    }
                }
                else coin.distribution = (coin.slider / maxSlider * 100).toFixed(precision);
            } else {
                if (coin.slider === -1) coin.distribution = (0).toFixed(precision);
                else coin.distribution = (coin.slider / maxSlider * 100).toFixed(precision);
            }
        });

        setPortfolio(newPortfolio);
        onInput(newPortfolio);
    };

    const handleOffset = (event, coin) => {
        const pattern = /^-?([0-9]*)((\.|,)\d{0,8})?$/
        let value = event.target.value;
        if (pattern.test(value)) {
            // Handle commas in input
            if (value.includes(',')) value = value.replace(',', '.');
            let newPortfolio = [...portfolio];
            const offset = value;

            let match = newPortfolio.find(c => c.coin === coin);
            match.offset = offset;

            newPortfolio = updateCurrentDistribution(newPortfolio);
            setPortfolio(newPortfolio);

            // Detect offset changes

            let original = configuration.portfolio.find(c => c.coin === coin);

            // In case of first-time setup (!original) set the original offset to zero so any change is detected
            if (!original) {
                original = {
                    offset: 0,
                };
            }

            if (original.offset !== offset) {
                let match = offsetChangeRef.current.find(c => c.coin === coin);

                if (match) {
                    match.change = offset - original.offset;
                } else {
                    offsetChangeRef.current.push({
                        coin: coin,
                        change: offset - original.offset,
                    });
                }
            } else {
                let match = offsetChangeRef.current.find(c => c.coin === coin);

                if (match) {
                    match.change = 0;
                }
            }

            props.callback({
                field: 'offsetChange',
                input: offsetChangeRef.current,
            });
        }
    }

    const handleBlur = (coin) => {
        let newPortfolio = [...portfolio];
        // offset is a string during entry, to get full control on the entered values
        // when leaving the input field, convert to number, and update input
        const match = portfolio.find((c) => c.coin === coin);
        const offset = (match.offset === '-' || match.offset === '.') ? Number(0) : Number(match.offset);
        match.offset = offset;

        setPortfolio(newPortfolio);
        onInput(newPortfolio);
    }

    const handleDelete = (coin) => {
        setDeleteCoin(coin);
        setOpenDelete(true);
    }

    const confirmDelete = (coin) => {
        // Remove from excluded list at the top of the table
        handleClick(coin);

        let newPortfolio = [...portfolio];
        const index = portfolio.findIndex((c) => c.coin === coin);
        newPortfolio.splice(index, 1);
        setPortfolio(newPortfolio);
        onInput(newPortfolio);
    }

    const labelFormatter = (value) => {
        if (value === -1) return '=';
        return value;
    }

    const isSelected = (coin) => selected.indexOf(coin) !== -1;

    const [open, setOpen] = useState(false);

    const handleTooltipClose = () => {
        setOpen(false);
    };

    const handleTooltipOpen = () => {
        setOpen(true);
    };

    return (
        <React.Fragment>
            <Dialog open={openDelete} onClose={() => setOpenDelete(false)}>
                <DialogTitle>Confirm Deletion</DialogTitle>
                <DialogContent>
                    <DialogContentText>
                        Are you sure you want to delete {deleteCoin} from this list?
                    </DialogContentText>
                </DialogContent>
                <DialogActions>
                    <Button variant="contained" onClick={() => setOpenDelete(false)} color={(mode === 'light') ? "primary" : "secondary"} >
                        Cancel
                    </Button>
                    <Button variant="contained" onClick={() => { setOpenDelete(false); confirmDelete(deleteCoin) }} color={(mode === 'light') ? "primary" : "secondary"} >
                        Delete
                    </Button>
                </DialogActions>
            </Dialog>
            <Grid container alignItems="flex-end">
                <Grid item xs={11}>
                    <Typography variant="h6" gutterBottom>
                        Coin Settings
                    </Typography>
                </Grid>
                <Grid item xs={1}>
                    <ClickAwayListener onClickAway={handleTooltipClose}>
                        <HtmlTooltip
                            title={
                                <React.Fragment>
                                    <Box m={2}>
                                        <div><b>{"Base coin"}</b></div>
                                        <div>{"Set the base coin used for all trades. Supported base coins are BTC, ETH, PAXG, BUSD, USDT, TUSD, USDC, USDP, USD and EUR. "}<i>{"Make sure your portfolio contains some of your base coin of choice"}</i>{". Only then it will appear as an option in the dropdown menu."}</div>
                                        <div>&nbsp;</div>
                                        <div><b>{"Distribution Mode"}</b></div>
                                        <div>{"Using the sliders, you can manually adjust the coin distribution. For sliders set completely to the left (showing = on the slider), you can determine how those coins are distributed. By default, those coins are distributed equally. When you select Market Cap, Balance Bot will automatically set the distribution based on the market cap of these coins. The √Market Cap option uses the square root of the market cap, which has a smoothening effect, to determine the distribution. The market cap data will be retrieved once per hour, and the distribution will be updated accordingly."}</div>
                                        <div>&nbsp;</div>
                                        <div>{"NOTE: to use this function, you need to create a free account on CoinMarketCap ("}<Link href="https://coinmarketcap.com/api" target="_blank" rel="noopener" color="secondary">coinmarketcap.com/api</Link>{") and enter your CoinMarketCap API key in the Bot Manager settings."}</div>
                                        <div>&nbsp;</div>
                                        <div><b>{"Exclude coins"}</b></div>
                                        <div>{"Use the checkboxes on the left to exclude coins in your account from balancing. Balance Bot will completely ignore selected coins."}</div>
                                        <div>&nbsp;</div>
                                        <div><b>{"Coin distribution"}</b></div>
                                        <div>{"By default, all coins are balanced with an equal distribution. If you'd like to change the Target Distribution of your coins, use the sliders. Coins with their slider all the way to the left will get an even distribution up to a total of 100%. The Current Distribution column shows how the coins in your portfolio are currently distributed."}</div>
                                        <div>&nbsp;</div>
                                        <div><b>{"Coin offset"}</b></div>
                                        <div>{"If you'd like to store part of your coins outside of your exchange spot wallet (e.g. on a hardware wallet or in a staking wallet), and you still want those coins to be used for balancing, you can specify the number of those 'external' coins in the Coin Offset column. This obviously has an immediate effect on the coin distribution, so please make sure you adjust the target distribution accordingly."}</div>
                                        <div>&nbsp;</div>
                                        <div><i>{"If you move coins out of your spot wallet, make sure that sufficient liquidity remains to execute balancing orders."}</i></div>
                                        <div>&nbsp;</div>
                                        <div>{"If you'd like to "}<i>{"exclude"}</i>{" a share of the coins you hold in your spot wallet from balancing, you can use a negative offset."}</div>
                                        <div>&nbsp;</div>
                                        <div><i>{"Keep an eye on your portfolio over time to make sure the number of coins used for balancing doesn't get too small."}</i></div>
                                    </Box>
                                </React.Fragment>
                            }
                            onClose={handleTooltipClose}
                            open={open}
                            disableFocusListener
                            disableHoverListener
                            disableTouchListener
                            interactive
                        >
                            <IconButton onClick={open ? handleTooltipClose : handleTooltipOpen}>
                                <InfoOutlinedIcon color="secondary" />
                            </IconButton>
                        </HtmlTooltip>
                    </ClickAwayListener>
                </Grid>
            </Grid>
            <Grid container spacing={3}>
                {(coinsReceived || input.basecoin !== '') ? (
                    <Grid container spacing={3}>
                        <Grid item xs={5}>
                            <Box pt={3} pl={2}>
                                <FormControl>
                                    <InputLabel>Base Coin</InputLabel>
                                    <Select
                                        labelId="base-coin"
                                        name="basecoin"
                                        value={basecoin}
                                        onChange={onBasecoin}
                                        className={classes.textinput}
                                        disabled={basecoins.length === 0 || input.configstate !== 'initial'}
                                    >
                                        {basecoins.map((coin, i) => {
                                            return <MenuItem key={i} value={coin}>{coin}</MenuItem>
                                        })}
                                    </Select>
                                    <FormHelperText>Base coin cannot be changed after initial bot configuration</FormHelperText>
                                </FormControl>
                            </Box>
                        </Grid>
                        <Grid item xs={7}>
                            <Box pt={3} pl={6}>
                                <FormControl component="fieldset">
                                    <FormLabel component="legend" className={classes.labelStyle}>Distribution Mode</FormLabel>
                                    <RadioGroup row aria-label="distribution" name="distribution" value={marketcapEnabled} onChange={onMarketcap}>
                                        <FormControlLabel value={false} control={<Radio />} label="Equal" />
                                        <FormControlLabel value="marketcap" control={<Radio />} label="Market Cap" disabled={!configuration.marketcapApi} />
                                        <FormControlLabel value="marketcapsqrt" control={<Radio />} label="√Market Cap" disabled={!configuration.marketcapApi} />
                                    </RadioGroup>
                                    <FormHelperText>For coins with their slider set to the left</FormHelperText>
                                </FormControl>
                                {(!configuration.marketcapApi) && (
                                    <FormHelperText style={{ color: 'red' }} >NOTE: store a CoinMarketCap API key in the Bot Manager settings to use Market Cap</FormHelperText>
                                )}
                            </Box>
                        </Grid>
                        <Grid item xs={12}>
                            {(basecoins.length === 0) && (
                                <Box pl={2} pb={4}>
                                    <Typography color="secondary">NOTE: you need to include some base coin in your portfolio</Typography>
                                </Box>
                            )}
                        </Grid>
                    </Grid>
                ) : (
                    <div>
                        <Box pt={4} pl={2}>
                            <Typography>Loading portfolio data...</Typography>
                        </Box>
                        <Box pt={4} pl={2}>
                            <Skeleton animation="wave" />
                        </Box>
                    </div>
                )}

                <Paper className={classes.paper} elevation={0}>
                    {(portfolioReceived && input.basecoin !== '') ? (
                        <div>
                            <EnhancedTableToolbar selected={selected} portfolio={portfolio} setSelected={setSelected} />
                            < Table
                                className={classes.table}
                                size="small"
                                aria-labelledby="tableTitle"
                                aria-label="enhanced table"
                            >
                                <EnhancedTableHead
                                    classes={classes}
                                    numSelected={selected.length}
                                    onSelectAllClick={handleSelectAllClick}
                                    rowCount={portfolio.length}
                                />
                                <TableBody>
                                    {portfolio.map((coin, index) => {
                                        const isItemSelected = isSelected(coin.coin);
                                        const labelId = `enhanced-table-checkbox-${index}`;
                                        let checkbox;
                                        if (coin.coin !== input.basecoin) {
                                            checkbox = <Checkbox
                                                checked={isItemSelected}
                                                inputProps={{ 'aria-labelledby': labelId }}
                                            />
                                        } else {
                                            checkbox = null;
                                        }

                                        return (
                                            <TableRow
                                                hover
                                                role="checkbox"
                                                aria-checked={isItemSelected}
                                                tabIndex={-1}
                                                key={coin.coin}
                                                selected={isItemSelected}
                                            >
                                                <TableCell padding="checkbox" onClick={(event) => handleClick(coin.coin)}>
                                                    {checkbox}
                                                </TableCell>
                                                <TableCell component="th" id={labelId} scope="row" padding="none" onClick={(event) => handleClick(coin.coin)}>
                                                    {coin.coin}
                                                </TableCell>
                                                <TableCell align="right">
                                                    <MySlider
                                                        value={coin.slider}
                                                        step={1}
                                                        valueLabelDisplay="on"
                                                        marks={marks}
                                                        min={-1}
                                                        valueLabelFormat={value => <div>{labelFormatter(value)}</div>}
                                                        onChange={(event, value) => handleSlider(value, coin.coin)}
                                                    />
                                                </TableCell>
                                                <TableCell component="th" className={classes.cell} id={labelId} scope="row" padding="none" align="left">
                                                    {coin.distribution}%
                                                </TableCell>
                                                <TableCell component="th" className={classes.cell} id={labelId} scope="row" padding="none" align="left">
                                                    {coin.currentDistribution}%
                                                </TableCell>
                                                <TableCell component="th" className={classes.cell} id={labelId} scope="row" padding="none" align="left">
                                                    <form className={classes.textinput2} noValidate autoComplete="off">
                                                        <Input id="offset" disableUnderline inputProps={{ style: { fontSize: 14, textAlign: 'right' } }}
                                                            value={coin.offset}
                                                            onChange={(event) => handleOffset(event, coin.coin)}
                                                            onBlur={() => handleBlur(coin.coin)} />
                                                    </form>
                                                </TableCell>
                                                <TableCell component="th" className={classes.cell2} id={labelId} scope="row" padding="none" align="left">
                                                    {(coin.coin === input.basecoin && parseFloat(coin.distribution) === 0) ? (
                                                        <Typography variant="overline" color="secondary">&nbsp;Cannot be 0%</Typography>
                                                    ) : (null)
                                                    }
                                                </TableCell>
                                                <TableCell component="th" className={classes.cell} id={labelId} scope="row" padding="none" align="left">
                                                    {(coin.excluded && parseFloat(coin.number) === 0) ? (
                                                        <LightTooltip title="Delete zero-balance coin">
                                                            <IconButton size="small" onClick={() => handleDelete(coin.coin)}>
                                                                <DeleteIcon color="secondary" />
                                                            </IconButton>
                                                        </LightTooltip>
                                                    ) : (null)
                                                    }
                                                </TableCell>
                                            </TableRow>
                                        )
                                    })}
                                </TableBody>
                            </Table>
                        </div>
                    ) : (input.basecoin !== '') && (
                        <div>
                            <Box pt={4} pl={2}>
                                <Typography>Loading portfolio data...</Typography>
                            </Box>
                            <Box pt={4} pl={2}>
                                <Skeleton animation="wave" />
                                <Skeleton animation="wave" />
                                <Skeleton animation="wave" />
                            </Box>
                        </div>
                    )}
                </Paper>
            </Grid>
        </React.Fragment>
    );
}